import '../App';


export default function HelpComponent(){
    return(
        <div className='help-container'>
            <h2>Incunae to program sucy do automatycznego transliterowania dawnych tekstw.</h2>
            <p>Dziki optycznemu rozpoznawaniu znakw (OCR) oraz modelom opartym na uczeniu maszynowym program odczytuje druki wydawane od XVI wieku.</p>
            <p>Niestety nie istniej jeszcze modele stworzone stricte dla polskich znakw, konieczne jest korzystanie z modeli niemieckich. W polu Wybierz model dostpne s cztery modele wytrenowane przez pracownikw Biblioteki Uniwersytetu w Mannheim. Maj one dobr skuteczno, jednak w zdecydowanej wikszoci nie rozpoznaj poprawnie samogosek pochylonych. Sytuacj poprawia znajdujca si w edytorze funkcja znajd pochylone, ktra automatycznie zmienia nierozpoznane znaki na samogoski pochylone.</p>
        </div>
    )
}